import Vue from 'vue'
import VueRouter from 'vue-router'
import https from '../utils/API/main_http.js'
import Index from '../views/menus/home/index.vue'

import SimplePassage from '../views/menus/home/SimplePassage.vue'
Vue.use(VueRouter)

// request main host
Vue.prototype.$https = https
Vue.prototype.$http = 'http://localhost:8080/api'

const routes = [
  {
    name: 'home',
    path: '/',
    component: Index,
    meta: {
      title: 'Straw'
    }
  },
  {
    name: 'pages',
    path: '/pages',
    meta: {
      title: 'Straw'
    },
    component: SimplePassage
  }
]
const router = new VueRouter({
  mode: 'history',
  routes
})
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */

  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
export default router
