<template>
  <div id="main" class="main-warp">
    <div class="main-content">
      <div class="formDiv">
        <i class="el-icon-close" @click="hideRegisterLogon" style="float: right; color: rgb(158 152 152)"></i>
        <h2 class="title text-center">忘记密码</h2>

        <form id="" role="forgetForm" class="form-horizontal" action="" method="post">
          <!-- <div class="form-item">
            <div class="input-warp"><span class="input-icon iconfont icon-yonghu1"></span><input id="userName" name="userName" placeholder="手机号码" type="text" class="inputs" value="" /></div>
            <p id="userNameErr" class="errMsg"></p>
          </div> -->
          <div class="form-item">
            <div class="input-warp"><span class="input-icon iconfont icon-youxiangbangding"></span><input id="email" name="email" placeholder="邮箱地址" type="text" class="inputs" value="" v-model="formForget.email" /></div>
            <p id="emailErr" class="errMsg"></p>
          </div>
          <div class="form-item">
            <div class="input-warp s">
              <span class="input-icon iconfont icon-yanzhengma"></span>
              <input id="kaptcha" name="kaptcha" placeholder="验证码" type="text" class="inputs" value="" v-model="formForget.PIN" />
              <div id="popup-captcha" style="display: none"></div>
            </div>
            <div class="pull-right" style="display: inline-block; margin-top: -1px">
              <button id="btnSendCode" class="btn lgbtn blue" @click.prevent="getPIN">获取验证码</button>
            </div>
            <p id="kaptchaErr" class="errMsg"></p>
          </div>

          <div class="form-item">
            <div class="input-warp">
              <span class="input-icon iconfont icon-baomi"></span>
              <input id="pwd" type="password" placeholder="新密码--至少6位字母或数字" maxlength="20" name="password" value="" class="inputs" v-model="formForget.PW" />
            </div>
            <p id="passwordErr" class="errMsg" v-html="formForget.PW ? (formForget.PW.length > 5 ? '' : '密码应不小于6位') : ''"></p>
          </div>
          <div class="form-item">
            <div class="input-warp">
              <span class="input-icon iconfont icon-baomi"></span>
              <input id="repwd" type="password" placeholder="重复密码--与新密码保持一致" maxlength="20" name="password2" value="" class="inputs" v-model="formForget.rePW" />
            </div>
            <p id="passwordErr2" class="errMsg" v-text="formForget.rePW ? (formForget.rePW === formForget.PW ? (formForget.rePW.length > 5 ? '' : '密码应不小于6位') : '两次密码不一致') : ''"></p>
          </div>
          <div class="btn-warp">
            <div class="text-center">
              <button type="button" id="btnSubmit" class="btn lgbtn blue btn-block" @click.prevent="sendForm">提交</button>
              <input id="appName" name="appName" type="hidden" value="jsform" />
            </div>
          </div>
          <div class="agreement gap">
            <div class="text-right">
              <div class="pull-left" style="cursor: pointer">记得密码，<a @click.stop="showLogon" class="link">登录</a></div>
              <p>重设密码后之前的密码将失效<a href="javascript:;" target="_blank" class="link"></a></p>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Eventbus from '../../utils/constant/Eventbus.js'
import qs from 'qs'
import re from '../../utils/regex'
import Base64 from 'base-64'
export default {
  name: 'Forget',
  data() {
    return {
      formForget: {
        email: '',
        PIN: '',
        PW: '',
        rePW: ''
      },
      formForgetEncode: {}
    }
  },
  watch: {
    formForget: {
      deep: true, // 开启深度监视,即如果ishot对象,可以监视ishot内部的值的变化
      handler(newvalue, oldvalue) {
        this.formForgetEncode.PW = Base64.encode(newvalue.PW)
        this.formForgetEncode.rePW = Base64.encode(newvalue.rePW)
        this.formForgetEncode.PIN = Base64.encode(newvalue.PIN)
        this.formForgetEncode.email = Base64.encode(newvalue.email)
      }
    },
    'formForget.PW': {
      handler(newvalue, oldvalue) {
        if (newvalue.length <= 6) {
          document.getElementById('passwordErr').innerText = '密码位数错误'
        }
      }
    }
  },
  methods: {
    clearForm() {
      this.formForget.email = ''
      this.formForget.PIN = ''
      this.formForget.PW = ''
      this.formForget.rePW = ''
    },
    showLogon() {
      this.$bus.$emit(Eventbus.register_logon, 'Logon')
    },
    hideRegisterLogon() {
      this.$emit('hideRegisterLogon', true)
    },
    getPIN(e) {
      if (re.emailRegExp.test(this.formForget.email)) {
        if (e.target.innerHTML === '获取验证码') {
          let time = 30
          axios.get(this.$http + `/logon/send/false?email=${this.formForget.email}`).then(val => {
            if (val.data.code !== 200) {
              this.$message.error('该邮箱未注册, 请先完成注册！')
            } else {
              this.$message.success('邮件发送成功, 请注意查收！')
              const timer = setInterval(() => {
                e.target.innerHTML = `${time}秒后再试`
                if (time === 0) {
                  e.target.innerHTML = '获取验证码'
                  clearInterval(timer)
                }
                time -= 1
              }, 1000)
              this.PIN = Object.values(val.data)[0]
              sessionStorage.setItem(Base64.decode(Object.keys(val.data)[0]), Base64.decode(Object.values(val.data)[0]))
              document.cookie = `${Object.keys(val.data)[0]}=${Object.values(val.data)[0]}`
            }
          })
        }
      } else {
        this.$message.error('邮箱格式错误')
      }
    },
    sendForm() {
      if (!this.PIN) {
        this.$message.error('请先获取验证码')
      } else if (this.formForget.PIN !== Base64.decode(this.PIN)) {
        this.$message.error('验证码有误，请重新填写')
      } else if (!re.emailRegExp.test(this.formForget.email)) {
        this.$message.error('邮箱格式错误，请重新填写')
      } else if (this.formForget.PW !== this.formForget.rePW || this.formForget.PW.length < 6) {
        this.$message.error('密码格式错误, 请修正')
      } else {
        const loading = this.$loading({
          lock: true,
          text: '请求中,请稍后...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        axios
          .post(this.$http + '/logon/forget', qs.stringify(this.formForgetEncode))
          .then(res => {
            loading.close()
            if (res.data.code === 200) {
              this.$message.success('恭喜您, 更新成功！')
              this.showLogon()
              this.clearForm()
            } else {
              this.$message.error('请检查您的邮箱是否正确！')
            }
          })
          .catch(e => {})
      }
    }
  }
}
</script>
<style scoped src="../../../public/externalCSS/register_logon/css/bootstrap.min.css"></style>
<style scoped src="../../../public/externalCSS/register_logon/css/a3common.css"></style>
<style scoped src="../../../public/externalCSS/register_logon/css/font_43459_lbtux0zjkr6yldi.css"></style>
<style scoped src="../../../public/externalCSS//register_logon/css/register.css"></style>
<style lang="less" scoped>
.errMsg {
  display: block;
  height: 20px;
  margin: 0;
  line-height: 20px;
  font-size: 14.5px !important;
}
</style>
