<template>
  <div class="header" :style="headerHeight ? `height:${headerHeight}` : 'height:16.5vh'">
    <van-nav-bar @click-right="onClickRight" :style="vanNavBarHeight ? `height:${vanNavBarHeight}` : 'height:15vh'">
      <template #left>
        <slot name="header-left">
          <van-image round :width="headerLeftWidth ? headerLeftWidth : '10vmin'" :height="headerLeftHeight ? headerLeftHeight : '10vmin'" fit="cover" src="https://img01.yzcdn.cn/vant/cat.jpeg" />
        </slot>
      </template>
      <template #title>
        <slot name="header-center">
          <van-search v-model="value" shape="round" background="transparent" placeholder="请输入搜索关键词" />
        </slot>
      </template>
      <template #right>
        <slot name="header-right">
          <van-icon v-if="hideUserCenter" name="user-circle-o" :color="logonColor ? logonColor : '#fff'" />
          <van-image v-else round :width="headerRightWidth ? headerRightWidth : '10vh'" :height="headerRightHeight ? headerRightHeight : '10vh'" fit="cover" :src="defaultUserIconPath" alt="用户中心" />
        </slot>
      </template>
    </van-nav-bar>

    <van-overlay :show="show" @click="show = false">
      <div class="wrapper 1234" @click.stop>
        <register></register>
        <component :is="register_logon" @hideRegisterLogon="hideRegisterLogon"></component>
      </div>
    </van-overlay>
  </div>
</template>
<script>
import Eventbus from '../../utils/constant/Eventbus.js'
import myfunc from '../../utils/myfunc'
import Logon from '../logon/Logon.vue'
import Base64 from 'base-64'
import Register from '../register/Register.vue'
import Forget from '../forget/Forget.vue'
export default {
  components: { Logon, Register, Forget },
  props: ['header-left-width', 'header-left-height', 'header-right-height', 'header-right-width', 'header-height', 'van-nav-bar-height', 'logon-color'],
  data() {
    return {
      value: '',
      show: false,
      register_logon: 'Logon',
      hideUserCenter: true,
      defaultUserIconPath: require('../../../public/images/defaultUserIcon.jpeg')
    }
  },
  methods: {
    onClickRight() {
      if (myfunc.getCookie(Base64.encode('if_logon')) === Base64.encode(Base64.encode('lsaaearUI&UIO$1scOP'))) {
        alert('user-center')
        return
      }
      this.show = true
      this.register_logon = 'Logon'
      this.$bus.$emit(Eventbus.showVertival, false)
      document.body.parentNode.style.overflowY = 'hidden'
    },
    hideRegisterLogon(value, email) {
      document.body.parentNode.style.overflowY = 'visible'
      this.show = false
      if (email) {
        this.hideUserCenter = false
      }
      this.$bus.$emit(Eventbus.showVertival, true)
    }
  },
  mounted() {
    this.$bus.$on(Eventbus.register_logon, value => {
      this.register_logon = value
    })
  }
}
</script>

<style lang="less" scope>
.header {
  width: 100%;
  margin: 0 auto;
  height: 16.5vh;
  position: relative;
  .van-nav-bar {
    top: 0;
    height: 15vh;
    width: 97vw;
    position: absolute;
    top: 1.5vh;
    left: 1vw;
  }
  .van-nav-bar__left {
    padding-left: 5px;
  }
  .van-nav-bar__content {
    height: 100%;
  }
  .van-search__content {
    padding: 0;
  }
  .van-search {
    width: 40vw;
    height: 100%;
  }
  .van-cell {
    height: 6vh;
    line-height: 5vh;
    margin-left: 1vw;
  }
  .van-nav-bar .van-icon {
    font-size: 2.5vh;
  }
  .van-nav-bar .van-nav-bar__right .van-icon {
    font-size: 8vh;
    color: #fff;
  }

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    z-index: 3;
  }
}
</style>
