<script>
import Vertical from './components/vertical/vertical.vue'
import Eventbus from './utils/constant/Eventbus.js'
export default {
  components: {
    // Header,
    Vertical
  },
  data() {
    return {
      component: 'Vertical',
      PassagePosition: false,
      ifshowpassage: false,
      title: '',
      list: '',
      focus: '',
      ifShowVertival: true
    }
  },
  created() {
    document.body.parentNode.style.overflowX = 'hidden'
    document.body.addEventListener(
      'click',
      function (event) {
        console.log(event.target.innertText)
        if (event.target.innertText) {
          console.log(event.target.innertText)
        } else if (event.target.getAttribute('src')) {
          console.log(event.target.getAttribute('src'))
        }
      },
      false
    )
  },
  mounted() {
    this.$refs.verticalComponent.$bus.$on(Eventbus.showVertival, value => {
      this.ifShowVertival = value
    })
  },
  methods: {
    changeLeftbar(val) {
      this.component = val
    },
    // 触摸导航时展示文章
    getScrollTop() {
      var scrollPos
      if (window.pageYOffset) {
        scrollPos = window.pageYOffset
      } else if (document.compatMode && document.compatMode !== 'BackCompat') {
        scrollPos = document.documentElement.scrollTop
      } else if (document.body) {
        scrollPos = document.body.scrollTop
      }
      return scrollPos
    }
  }
}
</script>
