<template>
  <div class="vertical_bar">
    <!-- 这里接受一个item_list数组,且里面的每一个item项需要包含以下属性, 其中child属性对应二级标题
      {
        url: String,
        id: Number,
        title: String
        child_list: [
          {
          url: String,
          id: String,
          title: String
        }
        ]
      }
     -->
    <div class="title" @mouseleave="hover_over" ref="title" :class="hide_animation">
      <div class="item_list fir_title">
        <!-- 切换的左侧导航栏到有滚动条的导航栏 -->
        <div class="switch">
          <van-nav-bar :border="false" class="tag" @click-left="onClickLeft" @click-right="onClickRight" active="false">
            <template #left>
              <van-icon ref="tag" name="down" />
            </template>
            <template #right>
              <router-link to="/">首页</router-link>
              <!-- <van-icon name="exchange" v-show="if_ellipsis" /> -->
            </template>
          </van-nav-bar>
        </div>
        <!-- 生成导航栏 -->
        <router-link to="/" v-for="item in item_list" :key="item.id" v-show="if_ellipsis">
          <div check="false" class="firtitle" @mouseover.prevent="show_pages" @mouseenter="hover" @mousemove="addanimation" @mouseout="stop" :id="`title_${item.id}`">{{ item.title }}</div>
        </router-link>
      </div>
      <div class="if_show_sectitle sec_title" ref="sectitle" v-show="if_ellipsis">
        <div class="container">
          <router-link
            :to="{
              path: '/pages',
              query: {
                title: item.title
              }
            }"
            v-for="item in item_child"
            :key="item.id"
          >
            <div class="child_list sectitle" @mouseenter.prevent="show_three_title" @mouseover.prevent="show_pages">{{ item.title }}</div>
          </router-link>
        </div>
      </div>
      <!-- three_title -->
      <div class="thr_title" v-show="item_three">
        <div>
          <router-link
            :to="{
              path: '/pages',
              query: {
                title: item.title
              }
            }"
            v-for="item in item_three"
            :key="item.id"
          >
            <div class="sectitle" style="text-align: left" @mouseenter.prevent="get_three_item" @mouseover.prevent="show_pages($event, true)">{{ item.title }}</div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      item_list: Array,
      item_child: Array,
      item_three: Array,
      show: false,
      on: true,
      if_ellipsis: true,
      hide: false, // 侦听器数据源
      hide_animation: '', // 动态设置navbar的隐藏动画
      attribute_hide: false // 初始不隐藏
    }
  },
  watch: {
    hide: function () {
      this.onClickLeft(true)
    }
  },

  methods: {
    show_pages(e, check = false) {
      if (check) {
        this.get_three_item()
      } else {
        this.current_item = this.fir_current_item
        this.target_title = this.fir_target
      }
      this.$mydata.item = this.current_item
      this.$mydata.focus = this.target_title
      console.log(this.$mydata.item, this.$mydata.focus)
      e.target.click()
    },
    get_three_item(e) {
      this.current_item = this.sec_current_item
      this.target_title = this.sec_taregt
    },
    show_three_title(e) {
      const title = e.target.innerText
      this.item_three = this.item_child.filter(item => item.title === title)[0].thr_list
      this.sec_current_item = this.item_three
      this.sec_taregt = title
    },
    hover(e) {
      if (e.target.getAttribute('check') === 'false') {
        e.target.setAttribute('check', 'true')
        this.show = true
        this.$refs.sectitle.classList.remove('outanimation')
        this.$refs.sectitle.classList.add('inanimation')
        this.$refs.sectitle.addEventListener('animationend', () => {
          this.on = true
        })
        this.$refs.sectitle.removeEventListener('animationend', () => {})
      }
      const id = e.target.getAttribute('id').slice(6)
      this.item_child = this.item_list.filter(item => String(item.id) === id)[0].child_list
      this.current_item = this.item_list.filter(item => String(item.id) === id)[0]
      this.target_title = e.target.innerText
      this.fir_current_item = this.current_item
      this.fir_target = this.target_title
      this.item_three = []
    },
    hover_over(e) {
      if (this.$refs.sectitle.className.split(' ').some(item => item === 'active')) {
        this.$refs.sectitle.classList.add('outanimation')
        this.$refs.sectitle.classList.remove('inanimation')
        this.item_three = []
      }
    },
    addanimation() {
      this.$refs.sectitle.classList.add('active')
    },
    stop(e) {
      e.target.setAttribute('check', 'false')
      this.show = false
    },
    onClickRight() {
      // this.$emit('hideVertical', 'VerticalScorll')
    },
    onClickLeft(check) {
      this.attribute_hide = !this.attribute_hide
      if (this.attribute_hide) {
        this.hide_animation = 'contract'
        this.if_ellipsis = false
      } else {
        this.if_ellipsis = true
        this.hide_animation = 'push'
      }
    },
    async initlistData() {
      const { data: res } = await this.$https.get('/menus/initmenus')
      this.item_list = res
    }
  },
  created() {
    this.initlistData()
    window.addEventListener(
      'scroll',
      () => {
        if (!this.attribute_hide) {
          this.hide = !this.hide
        }
      },
      true
    )
  }
}
</script>

<style scoped lang="less">
@margin-top: 1vh; // 距离上头部区域的距离
@max-height: 83.5vh-@margin-top; // 最大宽度, 注意这个不要随便改,因为其他的默认就是100%,也刚好占满屏幕
@line_height: @max-height*.1; // 行高
@firtitle: 15vh; // 一级标题的宽度
@sectitle: 50vh; // 二级标题的宽度
@firtitle_bgc: rgba(255, 255, 255, 0.01); // 一级标题背景颜色
@sectitle_bgc: transparent; // 二级标题背景颜色
@color: #fff; // 标题文字颜色
@hovercolor: transparent; // a 链接hover 时的背景色
@a_hover: rgb(226, 80, 104); // a 链接hover 时的文字颜色
@font-size: 2.25vh; // 文字大小及icon大小
@title:@firtitle+ @sectitle / 2; // 注意vue中会用到这个属性,如果更改其中任意一个属性,需要更改js中title
* {
  margin: 0;
  padding: 0;
  color: @color;
  -webkit-font-smoothing: antialiased;
}
.vertical_bar {
  margin-top: @margin-top;
  height: @max-height;
  position: absolute;
  overflow: hidden;
  float: left;
  //  左侧导航栏切换按钮样式
  .switch {
    width: 100%;
    .van-icon {
      font-size: @font-size;
      color: #fff;
    }
  }
  .switch .van-icon:hover {
    background-color: @hovercolor;
    color: @a_hover;
  }
  .title {
    position: fixed;
    height: 80%;
    width: @firtitle + @sectitle;
    top: 17vh;
    perspective-origin: left center;
    perspective: 600px;
    transform-style: preserve-3d;
    overflow: hidden;
    z-index: 5;
  }
  .fir_title {
    position: absolute;
    width: @firtitle;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 1;
    background-color: @firtitle_bgc;
  }
  #title_1 {
    margin-top: -1vh;
  }
  .firtitle {
    height: 10%;
    width: @firtitle;
    text-align: center;
    line-height: @line_height;
    font-size: @font-size;
  }
  .firtitle:hover {
    background-color: @hovercolor;
    font-weight: 600;
    color: @a_hover;
  }
  .sec_title {
    transition: all 0.5s linear;
    height: 100%;
    width: @sectitle;
    margin-left: -1px;
    position: absolute;
    top: 0;
    left: -(@sectitle / 2 - @firtitle);
    transform: rotateY(90deg);
    backface-visibility: hidden;
  }
  .thr_title {
    transition: all 0.5s linear;
    height: 100%;
    width: @sectitle / 2;
    margin-left: -1px;
    position: absolute;
    top: 0;
    left: 40.1vh;
    backface-visibility: hidden;
  }
  .container {
    margin-left: @sectitle / 2;
    height: 100%;
    background-color: @sectitle_bgc;
  }
  .sectitle:hover {
    background-color: @hovercolor;
    color: @a_hover;
  }
  .sectitle {
    text-align: center;
    float: left;
    width: 100%;
    height: 10%;
    line-height: @line_height;
    font-size: @font-size;
  }
  @keyframes sectitle_rotate_in {
    0% {
      transform: rotateY(90deg);
    }
    100% {
      transform: rotateY(0deg);
    }
  }
  @keyframes sectitle_rotate_out {
    0% {
      transform: rotateY(0deg);
    }
    100% {
      transform: rotateY(90deg);
    }
  }
  @keyframes contract {
    100% {
      width: @firtitle*2 / 5;
    }
  }
  @keyframes push {
    0% {
      width: @firtitle*2 / 5;
    }
    100% {
      width: @firtitle+ @sectitle;
    }
  }
}
.inanimation {
  animation: sectitle_rotate_in linear 0.5s 1 forwards;
}
.outanimation {
  animation: sectitle_rotate_out linear 0.5s 1 forwards;
}
.contract {
  animation: contract 0.3s linear forwards;
}
.push {
  animation: push linear 0.5s 1 forwards;
}
.active {
  transform: rotateY(0);
}
</style>
