      <Header header-left-height="8vmin" header-left-width="8vmin" header-right-height="7vmin" header-right-width="7vmin" header-height="100%" van-nav-bar-height="80%" logon-color="#1989fa">
        <template #header-left> </template>
        <template #header-center>
          <ul>
            <li class="sec_menus">{{ $mydata.focus }}</li>
            <li v-for="item in $mydata.item.child_list" :key="item.id" v-show="item" :class="item.title === $route.query.title ? 'focus' : false">
              <router-link :to="`/pages/?title=${item.title}`">
                <div @click="clearPagefocus">{{ item.title | myfilter(item.title, 5) }}</div>
              </router-link>
            </li>
            <li v-for="item in $mydata.item" :key="item.id" v-show="item.title" :class="item.title === $route.query.title ? 'focus' : false">
