<script>
import axios from 'axios'
import myfunc from '../../../utils/myfunc'
import Eventbus from '../../../utils/constant/Eventbus'
import Header from '../../../components/header/header.vue'
import Base64 from 'base-64'
export default {
  props: ['title'],
  components: { Header },
  data() {
    return {
      querytitle: this.$route.query,
      pages: [],
      pages_focus: '',
      count: 0
    }
  },
  watch: {
    $route: {
      deep: true, // 开启深度监视,即如果ishot对象,可以监视ishot内部的值的变化
      handler(newvalue, oldvalue) {
        this.initData()
        this.pages_focus = ''
      }
    }
  },
  created() {
    this.initData()
  },
  mounted() {
    const t = (window.innerWidth * 0.04 * (window.innerWidth * 0.04)) / (225 * window.innerWidth)
    const font = Math.sqrt(225 * window.innerWidth * t) + 1
    document.querySelector('.top_title').style.fontSize = String(font) + 'px'

    const t1 = (window.innerWidth * 0.02 * (window.innerWidth * 0.02)) / (225 * window.innerWidth)
    const font1 = Math.sqrt(225 * window.innerWidth * t1) + 1
    document.querySelector('.discribe_content').style.fontSize = String(font1) + 'px'

    window.onresize = () =>
      (() => {
        const font = Math.sqrt(225 * window.innerWidth * t) + 1

        const font1 = Math.sqrt(225 * window.innerWidth * t1) + 1

        document.querySelector('.top_title').style.fontSize = String(font) + 'px'

        document.querySelector('.discribe_content').style.fontSize = String(font1) + 'px'
      })()
    window.addEventListener(
      'scroll',
      () => {
        if (this.pages_focus && document.querySelector('.mobil_navbar').style) {
          if (document.querySelector('.mobil_navbar').style.height !== '45px') {
            document.querySelector('.mobil_navbar').style.height = '45px'
            this.count++
          }
        }
      },
      true
    )
  },
  methods: {
    hideRegisterLogon(value, email) {
      document.body.parentNode.style.overflowY = 'visible'
      this.show = false
      if (email) {
        this.hideUserCenter = false
      }
      this.$bus.$emit(Eventbus.showVertival, true)
    },
    onClickRight() {
      //  === Base64.encode(Base64.encode('lsaaearUI&UIO$1scOP'))
      if (myfunc.getCookie(Base64.encode('if_logon'))) {
        console.log('ttt==>', myfunc.getCookie(Base64.encode('if_logon')))
        alert('user-center')
        return
      }
      this.show = true
      this.register_logon = 'Logon'
      this.$bus.$emit(Eventbus.showVertival, false)
      document.body.parentNode.style.overflowY = 'hidden'
    },
    initData() {
      axios.get(this.$http + `/pages/${this.$route.query.title}/1`).then(value => {
        this.pages = value.data
      })
    },

    gotoPre() {
      if (this.pages[0].ifPrePage) {
        axios.get(this.$http + `/pages/${this.$route.query.title}/${this.pages[0].currentPage - 1}`).then(value => {
          this.pages = value.data
        })
      }
    },
    gotoNext() {
      if (this.pages[0].ifNextPage) {
        axios.get(this.$http + `/pages/${this.$route.query.title}/${this.pages[0].currentPage + 1}`).then(value => {
          this.pages = value.data
        })
      }
    },
    goto(value) {
      axios.get(this.$http + `/pages/${this.$route.query.title}/${value}`).then(value => {
        this.pages = value.data
      })
    },

    change_pagefocus(article) {
      this.pages_focus = article
      window.scrollTo(0, 0)
    },
    clearPagefocus() {
      this.pages_focus = ''
    },
    show_mobil_navbar() {
      console.log(this.count)
      if (!((this.count % 2) - 1)) {
        document.querySelector('.mobil_navbar').style.height = String(45 + this.pages.length * 35) + 'px'
        this.count++
      } else {
        document.querySelector('.mobil_navbar').style.height = String(Number(document.querySelector('.mobil_navbar').style.height.replace('px', '')) - this.pages.length * 35) + 'px'
        this.count++
      }
    }
  }
}
</script>

