<template>
  <div id="main" class="main-warp">
    <div class="main-content">
      <div class="formDiv">
        <i class="el-icon-close" @click="hideRegisterLogon" style="float: right; color: rgb(158 152 152)"></i>
        <h2 class="text-center">登录</h2>

        <form id="loginForm" method="post">
          <div class="dataform">
            <div class="input-warp gap">
              <span class="input-icon iconfont icon-youxiangbangding"></span>
              <input v-model="formLogon.email" id="userName" name="username" type="text" class="inputs" placeholder="邮箱地址" maxlength="64" />
            </div>
            <div class="error-content">
              <span id="userNameErr" class="errMsg"></span>
            </div>

            <div class="input-warp gap">
              <span class="input-icon iconfont icon-baomi"></span>
              <input v-model="formLogon.PW" class="inputs" type="password" name="password" placeholder="密码" id="pwd" maxlength="20" />
            </div>
            <div class="error-content">
              <span id="passwordErr" class="errMsg"></span>
            </div>

            <div class="btn-warp gap">
              <div class="text-center">
                <input type="hidden" value="jsform" id="_app" />
                <button type="submit" id="btnLogin" class="btn btn-block lgbtn blue" @click.prevent="sendFrom">登录</button>
              </div>
            </div>
            <div class="gap">
              <div class="pull-right" style="margin-top: 6px; cursor: pointer"><a href="javascript:;" class="link" @click.stop="showForegt">忘记密码</a><span class="split-space">|</span><a @click.stop="showRegister" class="link">新用户注册</a></div>

              <div class="pretty-box">
                <input type="checkbox" value="1" name="REMEMBER" id="remember" class="" />
                <label for="remember" style="font-weight: normal">记住我</label>
              </div>
            </div>

            <div class="biggap third-party-title">
              <h5 class="text-center"><span>第三方账号登录</span></h5>
            </div>
            <div class="third-auth">
              <a title="用钉钉登录" class="dt" href="javascript:;"></a>
              <a title="用微信账户登录" class="wx" href="javascript:;"></a>
              <a title="用QQ账户登录" class="qq" href="javascript:;"></a>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import re from '../../utils/regex'
import Eventbus from '../../utils/constant/Eventbus.js'
import Base64 from 'base-64'
import qs from 'qs'
export default {
  name: 'Logon',
  data() {
    return {
      formLogon: {
        email: '',
        PW: ''
      },
      formLogonEncode: {}
    }
  },
  watch: {
    formLogon: {
      deep: true,
      handler(newvalue) {
        this.formLogonEncode.PW = Base64.encode(newvalue.PW)
        this.formLogonEncode.email = Base64.encode(newvalue.email)
      }
    }
  },
  methods: {
    clearFrom() {
      this.formLogon.email = ''
      this.formLogon.PW = ''
    },
    showRegister() {
      this.$bus.$emit(Eventbus.register_logon, 'Register')
    },
    hideRegisterLogon() {
      this.clearFrom()
      this.$emit('hideRegisterLogon', true)
    },
    showForegt() {
      this.$bus.$emit(Eventbus.register_logon, 'Forget')
    },
    sendFrom() {
      if (re.emailRegExp.test(this.formLogon.email) && this.formLogon.PW) {
        const loading = this.$loading({
          lock: true,
          text: '请求中,请稍后...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        axios
          .post(this.$http + '/logon/logon', qs.stringify(this.formLogonEncode))
          .then(res => {
            console.log(res.data)
            loading.close()
            if (res.data.code === 200) {
              this.$message.success('恭喜您, 登录成功！')
              this.$emit('hideRegisterLogon', true, res.data)
              document.cookie = `${Base64.encode('if_logon')}=${Base64.encode(Base64.encode('lsaaearUI&UIO$1scOP'))}`
              this.clearFrom()
            } else {
              this.$message.error('邮箱或密码有误！')
            }
          })
          .catch(e => {})
      } else if (!re.emailRegExp.test(this.formLogon.email)) {
        this.$message.error('邮箱格式错误！')
      } else {
        this.$message.error('邮箱和密码均不能为空！')
      }
    }
  }
}
</script>
<style scoped src="../../../public/externalCSS/register_logon/css/bootstrap.min.css"></style>
<style scoped src="../../../public/externalCSS/register_logon/css/a3common.css"></style>
<style scoped src="../../../public/externalCSS/register_logon/css/font_43459_lbtux0zjkr6yldi.css"></style>
<style scoped src="../../../public/externalCSS//register_logon/css/login.css"></style>
