<template>
  <div id="main" class="main-warp">
    <div class="main-content">
      <div class="formDiv">
        <i class="el-icon-close" @click="hideRegisterLogon" style="float: right; color: rgb(158 152 152)"></i>
        <h2 class="title text-center">注册</h2>

        <form id="registerForm" role="form" class="form-horizontal" method="post">
          <div class="form-item">
            <div class="input-warp"><span class="input-icon iconfont icon-youxiangbangding"></span><input v-model="registerForm.email" id="email" name="email" placeholder="邮箱地址" type="text" class="inputs" value="" /></div>
            <p id="emailErr" class="errMsg"></p>
          </div>
          <div class="form-item">
            <div class="input-warp s">
              <span class="input-icon iconfont icon-yanzhengma"></span>
              <input id="kaptcha" v-model="registerForm.PIN" name="kaptcha" placeholder="验证码" type="text" class="inputs" value="" autocomplete="off" />
              <div id="popup-captcha" style="display: none"></div>
            </div>
            <div class="pull-right" style="display: inline-block; margin-top: -1px">
              <button class="btn lgbtn blue" @click.prevent="getPIN">获取验证码</button>
            </div>
            <p id="kaptchaErr" class="errMsg"></p>
          </div>

          <div class="form-item">
            <div class="input-warp">
              <span class="input-icon iconfont icon-baomi"></span>
              <input id="pwd" v-model="registerForm.PW" type="password" placeholder="至少6位字母或数字" maxlength="20" name="password" value="" class="inputs" />
            </div>
            <p id="passwordErr" class="errMsg" v-html="registerForm.PW ? (registerForm.PW.length > 5 ? '' : '密码应不小于6位') : ''"></p>
          </div>
          <div class="form-item">
            <div class="input-warp">
              <span class="input-icon iconfont icon-baomi"></span>
              <input id="repwd" v-model="registerForm.rePW" type="password" placeholder="请于密码保持一致" maxlength="20" name="password2" value="" class="inputs" />
            </div>
            <p id="passwordErr2" class="errMsg" v-text="registerForm.rePW ? (registerForm.rePW === registerForm.PW ? (registerForm.rePW.length > 5 ? '' : '密码应不小于6位') : '两次密码不一致') : ''"></p>
          </div>
          <!-- <div class="form-item">
            <div class="input-warp"><span class="input-icon iconfont icon-yonghu1"></span><input id="userName" name="userName" v-model="registerForm.userPhone" placeholder="手机号码" type="text" class="inputs" value="" /></div>
            <p id="userNameErr" class="errMsg"></p>
          </div> -->
          <div class="btn-warp">
            <div class="text-center">
              <button type="button" id="btnSubmit" class="btn lgbtn blue btn-block" @click.prevent="sendRegsiter">注册</button>
              <input id="appName" name="appName" type="hidden" value="jsform" />
            </div>
          </div>
          <div class="agreement gap">
            <div class="text-right">
              <div class="pull-left" style="cursor: pointer">已有账号，<a @click.stop="showLogon" class="link">登录</a></div>
              <p>注册即代表您已阅读并同意《<a href="javascript:;" target="_blank" class="link">使用协议</a>》</p>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Eventbus from '../../utils/constant/Eventbus.js'
import Base64 from 'base-64'
import qs from 'qs'
import re from '../../utils/regex'
export default {
  name: 'Register',
  data() {
    return {
      registerForm: {
        // userPhone: '', 因为使用的是邮箱, 故不必需要电话
        PIN: '',
        PW: '',
        rePW: '',
        email: ''
      },
      registerFromEncode: {}
    }
  },
  watch: {
    registerForm: {
      deep: true, // 开启深度监视,即如果ishot对象,可以监视ishot内部的值的变化
      handler(newvalue, oldvalue) {
        // this.registerFromEncode.userPhone = Base64.encode(newvalue.userPhone)
        this.registerFromEncode.PW = Base64.encode(newvalue.PW)
        this.registerFromEncode.rePW = Base64.encode(newvalue.rePW)
        this.registerFromEncode.PIN = Base64.encode(newvalue.PIN)
        this.registerFromEncode.email = Base64.encode(newvalue.email)
      }
    },
    'registerForm.PW': {
      handler(newvalue, oldvalue) {
        if (newvalue.length <= 6) {
          console.log('cmx', document.getElementById('passwordErr').innerText)
          document.getElementById('passwordErr').innerText = '密码位数错误'
        }
      }
    }
  },
  methods: {
    clearForm() {
      this.registerForm.email = ''
      this.registerForm.PIN = ''
      this.registerForm.PW = ''
      this.registerForm.rePW = ''
    },
    showLogon() {
      this.clearForm()
      this.$bus.$emit(Eventbus.register_logon, 'Logon')
    },
    hideRegisterLogon() {
      this.clearForm()
      this.$emit('hideRegisterLogon', true)
    },
    getPIN(e) {
      if (re.emailRegExp.test(this.registerForm.email)) {
        if (e.target.innerHTML === '获取验证码') {
          let time = 30
          axios.get(this.$http + `/logon/send/true?email=${this.registerForm.email}`).then(val => {
            if (val.data.code !== 200) {
              this.$message.error('该邮箱已注册, 无须重复注册')
            } else {
              this.$message.success('邮件发送成功, 请尽快完成注册')
              const timer = setInterval(() => {
                e.target.innerHTML = `${time}秒后再试`
                if (time === 0) {
                  e.target.innerHTML = '获取验证码'
                  clearInterval(timer)
                }
                time -= 1
              }, 1000)
              this.PIN = Object.values(val.data)[0]
              sessionStorage.setItem(Base64.decode(Object.keys(val.data)[0]), Base64.decode(Object.values(val.data)[0]))
              document.cookie = `${Object.keys(val.data)[0]}=${Object.values(val.data)[0]}`
              document.cookie = `${Base64.encode('if_logon')}=${Base64.encode(Base64.encode('lsaaearUI&UIO$1scOP'))}`
            }
          })
        }
      } else {
        this.$message.error('邮箱格式错误')
      }
    },
    sendRegsiter(e) {
      if (!this.PIN) {
        this.$message.error('请先获取验证码')
      } else if (this.registerForm.PIN !== Base64.decode(this.PIN)) {
        this.$message.error('验证码有误，请重新填写')
      } else if (!re.emailRegExp.test(this.registerForm.email)) {
        this.$message.error('邮箱格式错误，请重新填写')
      } else if (this.registerForm.PW !== this.registerForm.rePW || this.registerForm.PW.length < 6) {
        this.$message.error('密码格式错误, 请修正')
      } else {
        const loading = this.$loading({
          lock: true,
          text: '请求中,请稍后...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        axios
          .post(this.$http + '/logon/register', qs.stringify(this.registerFromEncode))
          .then(res => {
            loading.close()
            if (res.data.code !== 200) {
              this.$message.error(res.data.msg)
            } else {
              this.$message.success(res.data.msg)
              this.$emit('hideRegisterLogon', true, this.registerForm.email)
              this.clearForm()
            }
          })
          .catch(e => {})
      }
    }
  }
}
</script>
<style scoped src="../../../public/externalCSS/register_logon/css/bootstrap.min.css"></style>
<style scoped src="../../../public/externalCSS/register_logon/css/a3common.css"></style>
<style scoped src="../../../public/externalCSS/register_logon/css/font_43459_lbtux0zjkr6yldi.css"></style>
<style scoped src="../../../public/externalCSS//register_logon/css/register.css"></style>

<style lang="less" scoped>
.errMsg {
  display: block;
  height: 20px;
  margin: 0;
  line-height: 20px;
  font-size: 14.5px !important;
}
</style>
