import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Vant from 'vant'
import 'vant/lib/index.less'
import myplugins from './utils/myplugins/plugins.js'
import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'


Vue.use(Vant)
Vue.use(myplugins)
Vue.use(Element)
Vue.config.productionTip = false

Vue.prototype.$mydata = {
  target_title: String,
  items: Array
}
new Vue({
  router,
  render: h => h(App),
  beforeCreate() {
    Vue.prototype.$bus = this // 安装全局事件总线, $bus 就是当前的应用vm
  }
}
).$mount('#app')
